import React, { Component } from 'react';
import { Header, BackToMain, renderProfile } from './index';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';
import profilePic from './images/user2.svg';

class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            startDate:null,
            changeApp:false,
            nextAppointment: moment("25.02.2018", "DD.MM.YYYY"),
            lastAppointment: moment("23.11.2017", "DD.MM.YYYY")
        };
        this.changeAppointment = this.changeAppointment.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }
    componentDidMount() {
      this.setState({startDate:moment()})
    }
    changeAppointment() {
        this.setState({changeApp:true});
        renderProfile();
    }
    handleChange(date) {
        this.setState({
          nextAppointment: date
        });
    }
    render() {
        return (
            <div className="container">
                <div className="col-md-8 col-md-offset-3">
                    <div className="col-md-4 profilebox">
                        <img src={profilePic} alt="your profile picture" width="100%" height="100%"/>
                    </div>
                    <div style={{marginLeft: '20px'}} className="col-md-7 left-align">
                        <h4><strong>Name:</strong> {this.props.name}</h4>
                        <p><strong>Date of Birth:</strong> {this.props.dob}</p>
                        <p><strong>Address:</strong> Laamantie 5, 12345 Kuusamo</p>
                        <p><strong>Last appointment:</strong> {this.state.lastAppointment.format('DD.MM.YYYY')}</p>
                        <p><strong>Next appointment:</strong> {this.state.nextAppointment.format('DD.MM.YYYY')} <button className="btn-primary" onClick={this.changeAppointment}>Change</button></p>
                        {this.state.changeApp ? <div><strong>Suggest new appointment date:</strong> <DatePicker
                        dateFormat="DD.MM.YYYY"
                        selected={this.state.startDate}
                        onChange={this.handleChange}
                        /></div> : null}
                    </div>
                </div>
            </div>
        )
    }
};

export default class ProfilePage extends Component {
    render() {
        return (
            <div>
                <Header title="Irmeli Korhonen" />
                <Profile name="Irmeli Pivikki Korhonen" dob="3.5.1934" lastAppointment="25.11.2017" />
                <BackToMain />
            </div>
        )
    }
};


